import React, { useState } from "react";
import { TextField, Button, Typography, CircularProgress, Box, Paper, Card, CardContent, Grid, Chip } from "@mui/material";
import { db } from "../firebase"; // Firebase configuration
import { collection, query, where, getDocs } from "firebase/firestore";

const CustomerServiceStatus = () => {
  const [serviceId, setServiceId] = useState("");
  const [serviceDetails, setServiceDetails] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleServiceIdChange = (e) => {
    setServiceId(e.target.value);
  };

  const fetchServiceStatus = async () => {
    if (!serviceId) {
      alert("Please enter a service ID.");
      return;
    }
    setLoading(true);
    setError(null);
    try {
      const q = query(collection(db, "mobileServices"), where("serviceId", "==", serviceId));
      const querySnapshot = await getDocs(q);
      if (!querySnapshot.empty) {
        const serviceDoc = querySnapshot.docs[0].data(); 
        setServiceDetails(serviceDoc);
      } else {
        setError("Service ID not found.");
      }
    } catch (err) {
      setError("Error fetching service details. Please try again.");
    }
    setLoading(false);
  };

  return (
    <Box
      sx={{
        maxWidth: 800,
        margin: "0 auto",
        padding: 4,
        background: "#f5f5f5",
        borderRadius: 2,
        boxShadow: 3,
      }}
    >
      <Typography
        variant="h3"
        align="center"
        gutterBottom
        sx={{
          fontWeight: "bold",
          color: "#2575fc",
          marginBottom: 3,
        }}
      >
        Service Status Check
      </Typography>

      <Paper
        sx={{
          padding: 3,
          backgroundColor: "#ffffff",
          borderRadius: 2,
          boxShadow: 2,
          color: "#000",
          marginBottom: 4,
        }}
      >
        <Typography
          variant="h5"
          gutterBottom
          sx={{ color: "#2575fc", fontWeight: 600 }}
        >
          Enter your Service ID to track your service status:
        </Typography>
        <TextField
          label="Service ID"
          variant="outlined"
          fullWidth
          value={serviceId}
          onChange={handleServiceIdChange}
          sx={{
            marginBottom: 3,
            "& .MuiOutlinedInput-root": {
              borderRadius: "12px",
            },
            "& .MuiInputLabel-root": {
              color: "#2575fc",
            },
          }}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={fetchServiceStatus}
          fullWidth
          sx={{
            padding: "12px",
            borderRadius: "12px",
            backgroundColor: "#2575fc",
            "&:hover": {
              backgroundColor: "#6a11cb",
            },
          }}
        >
          Check Status
        </Button>
      </Paper>

      {loading && (
        <Box sx={{ textAlign: "center", marginTop: 3 }}>
          <CircularProgress color="primary" />
        </Box>
      )}

      {error && (
        <Typography
          variant="body1"
          color="error"
          align="center"
          sx={{ marginTop: 3 }}
        >
          {error}
        </Typography>
      )}

      {serviceDetails && !loading && (
        <Box sx={{ marginTop: 4 }}>
          <Grid container spacing={4} justifyContent="center">
            <Grid item xs={12} sm={8} md={6}>
              <Card
                variant="outlined"
                sx={{
                  backgroundColor: "#ffffff",
                  borderRadius: "12px",
                  boxShadow: 3,
                  padding: 3,
                }}
              >
                <CardContent>
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "bold",
                      color: "#2575fc",
                      marginBottom: 2,
                    }}
                  >
                    Service ID: {serviceDetails.serviceId}
                  </Typography>

                  <Typography
                    variant="h6"
                    sx={{
                      marginTop: 2,
                      fontWeight: "bold",
                      color: "#333",
                    }}
                  >
                    Customer Details:
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Customer Name:</strong> {serviceDetails.customerName}
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Address:</strong> {serviceDetails.customerAddress}
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Mobile No:</strong> {serviceDetails.customerMobile}
                  </Typography>

                  <Typography
                    variant="h6"
                    sx={{
                      marginTop: 3,
                      fontWeight: "bold",
                      color: "#333",
                    }}
                  >
                    Service Details:
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Mobile Model:</strong> {serviceDetails.mobileModel}
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Service Type:</strong> {serviceDetails.serviceType}
                  </Typography>
                  <Typography variant="body1" gutterBottom>
                    <strong>Estimated Amount:</strong> ₹{serviceDetails.estimatedAmount}
                  </Typography>

                  <Typography
                    variant="h6"
                    sx={{
                      marginTop: 3,
                      fontWeight: "bold",
                      color: "#333",
                    }}
                  >
                    Status:
                  </Typography>
                  <Chip
                    label={serviceDetails.status}
                    color={
                      serviceDetails.status === "Completed"
                        ? "success"
                        : serviceDetails.status === "In Progress"
                        ? "warning"
                        : "error"
                    }
                    sx={{
                      fontWeight: "bold",
                      fontSize: "1.1rem",
                      padding: "8px 16px",
                      marginTop: 1,
                    }}
                  />
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>
      )}
    </Box>
  );
};

export default CustomerServiceStatus;
